@font-face {
  font-family: "Poppins-Light";
  src: url(../assets/fonts/Poppins-Light.ttf);
}

@font-face {
  font-family: "Poppins-Medium";
  src: url(../assets/fonts/Poppins-Medium.ttf);
}

$font-1: Poppins-Light;
$font-2: Poppins-Medium;

$color-1:rgba(21, 24, 42, 0.828);
$color-2: rgb(21, 25, 43);
$color-3: #32d8e5;
$color-4:rgb(245, 239, 239);

$color-5: rgba(23, 44, 74,
  0.617);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;

}

body {
  background: $color-2 !important;
  overscroll-behavior: none;
  font-family: $font-2;
  font-size: 16px;
}

ul {
  list-style-type: none;
  display: flex;
}

a,
a:visited {
  color: inherit;
  text-decoration: none;
}

@mixin simple-center {
  display: flex;
  align-items: center;
  justify-content: center;

}

// glass effect mixin
@mixin glass-effect-1 {
  background: linear-gradient(135deg,
      rgba(252, 252, 252, 0.202),
      rgba(241, 245, 246, 0));
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px 0 rgba(193, 192, 192, 0.259);
  border: 2px solid rgba(255, 255, 255);
  border-radius: 5px;
}

@mixin glass-effect-2 {
  background: linear-gradient(135deg,
      rgba(252, 252, 252, 0.202),
      rgba(241, 245, 246, 0));
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px 0 rgba(193, 192, 192, 0.259);
  border-top: 3px solid rgb(255, 255, 255);
  border-left: 3px solid rgb(255, 255, 255);
  border-radius: 20px;
}

@mixin skill-glass-effect {
  background: linear-gradient(135deg,
      rgba(252, 252, 252, 0.202),
      rgba(241, 245, 246, 0));
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 7px 0 rgba(193, 192, 192, 0.259);
  border-top: 3px solid rgb(255, 255, 255);
  border-left: 3px solid rgb(255, 255, 255);
  border-radius: 20px;
}

.section-title {
  font-family: $font-2;
  font-size: 2.7rem;
  margin-bottom: 18px;
  color: $color-3;

  @media screen and (max-width: 768px) {
    font-size: 2.1rem;
  }
}

.link {
  padding: 10px 20px;
  border: 1px solid white;
  background: none;
  color: white;

  @media screen and (max-width: 768px) {
    padding: 7px 15px;
  }

  &:hover {
    border: 1px solid rgb(32, 128, 227);
    box-shadow: 0 0px 10px rgba(32, 129, 227, 0.7);
  }
}