.skills {
  position: relative;
  min-height: 100vh;
  padding: 65px 0;
  background: $color-2;
  z-index: 10;
  overflow: hidden;

  &::before {
    position: absolute;
    content: "";
    top: -390px;
    left: -12%;
    width: 100%;
    height: 400px;
    background: linear-gradient(to bottom, rgb(237,
          130,
          130) 34%, rgb(210, 2, 54) 100%);
    transform: rotate(-2deg);
    z-index: 0;
    border-radius: 50px;
  }

  &::after {
    position: absolute;
    content: "";
    top: -390px;
    right: -12%;
    width: 100%;
    height: 400px;
    background: linear-gradient(to bottom, rgb(237,
          130,
          130) 34%, rgb(210, 2, 54) 100%);
    transform: rotate(2deg);
    z-index: 0;
    border-radius: 50px;
  }


  .skills-container {
    width: 1100px;
    min-height: 100vh;
    max-width: 90%;
    margin: 0 auto;
    @include simple-center;
    z-index: 10;
    position: relative;

    .text-content {
      p {
        color: $color-4;
        font-family: $font-2;
        font-size: 1.2rem;
        margin-bottom: 48px;
      }
    }

    .skills-content {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      padding: 20px 0;

      .card-1 {
        flex: 1 0 20%;
        min-width: 140px;
        width: 140px;
        max-width: 160px;
        height: 140px;
        margin: 8px;
        padding: 5px;
        border-radius: 5px;
        background: inherit;
        font-family: $font-2;
        color: whitesmoke;
        letter-spacing: 1.5px;
        box-shadow: 0 5px 5px rgba(0, 0, 0, 0.961);
        @include simple-center;
        transition: 400ms ease-in-out;

        &:hover {
          border: 1px solid rgb(32, 128, 227);
          box-shadow: 0 0px 10px rgba(32, 129, 227, 0.7);
        }



        .container {
          width: 100%;
          height: 100%;
          display: grid;
          grid-template-rows: 60% 40%;
          padding: 0;

          .icon-container {
            width: 100%;
            @include simple-center;
            align-items: center;

            img {
              width: 45px;
              height: 45px;
            }
          }

          .skill-name {
            width: 100%;
            height: 100%;
            @include simple-center;
            padding-bottom: 5px;
            text-align: center;
            line-height: 18px;
            padding: 5px;
            font-family: $font-1;
          }
        }
      }

      .card-1:nth-child(2n) {
        border-bottom: 1px solid $color-3;
      }

      .card-1:nth-child(2n+1) {
        border-top: 1px solid $color-3;
      }
    }
  }
}